@use "./breakpoints" as *;
$black-color: #000;
$white-color: #fff;
$primary-color: #115e5c;
$text-color: #191817;

$font--family: "Assistant", sans-serif;
$font-montserrat: "Assistant", sans-serif;
$font-slab: "Yorkten Slab", sans-serif;
$font-gilroy: "Gilroy", sans-serif;

@keyframes sliderRight {
    from {
        opacity: 0;
        transform: translateX(calc(100% + 20px));
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes fadeOut {
    50% {
        opacity: 1;
    }
    75% {
        opacity: 1;
        transform: translate(5px, 5px);
    }
    100% {
        opacity: 0;
        transform: translate(calc(100% + 20px), 5px);
    }
}

@keyframes toOpacity {
    from {
        background-color: rgba(23, 38, 53, 0.1);
    }
    to {
        background-color: rgba(41, 54, 68, 0.35);
    }
}

@mixin show_all_width($width) {
    width: $width;
    &::before {
        width: $width * calc(10 / 4);
        height: $width * calc(10 / 4);
    }
}

@mixin show_all_beffor($width) {
    &::before {
        width: $width * calc(10 / 4);
        height: $width * calc(10 / 4);
    }
}
