.btn_show_all {
    $height: 4.6rem;
    height: $height;
    cursor: pointer;
    position: relative;
    border-radius: 10px;
    background-color: #fff;
    margin: 0 auto;
    overflow: hidden;
    z-index: 1;
    &::before {
        content: "";
        position: absolute;
        top: -50%;
        left: 50%;
        z-index: -1;
        border-radius: 50%;
        transform-origin: top center;
        transform: translate(-50%, -40%) scale(0.4);
        background-color: #115e5c;
        transition: transform 0.8s ease 0.2s;
    }
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: calc(100% - 2px);
        height: calc(100% - 2px);
        border: 1px solid #115e5c;
        border-radius: 10px;
    }

    &:hover {
        &::before {
            transform: translate(-50%, -50%) scale(1);
            transition: transform 0.9s ease;
            transform-origin: bottom center;
        }
        p {
            color: #fff;
            transition: all 0.3s ease;
        }
    }
    &_color {
        background-color: #fd7f28;
        text-transform: uppercase;
        padding: 0 12px;
        .btn_show_all_link {
            p {
                color: #fff;
            }
        }
    }
    &_link {
        text-decoration: none;
        text-align: center;
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 10px;
        line-height: normal;
        p {
            color: #105756;
            font-weight: 600;
            font-size: 1.6rem;
            font-family: $font-gilroy;

            transition: all 0.9s ease 0.2s;
            line-height: $height;
            z-index: 3;
        }
    }
    span {
        color: #125e5c;
        font-size: 1.4rem;
        font-weight: 600;
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 1;
        transform: translate(-50%, -50%);
        min-width: 110px;
        transition: all 0.3s linear;
        padding: 12px;
    }
    .btn_circle {
        //     position: absolute;
        //     top: 42px;
        //     left: -2px;
        width: 110px;
        height: 110px;
        //     z-index: 0;
        border-radius: 50%;
        background-color: #115e5c;
        transition: all 0.3s ease-in-out;
    }
}

