        background-color: rgba(41, 54, 68, 0.35);
    }
}

// Notice
#notice {
    position: fixed;
    top: 124px;
    right: 16px;
    z-index: 99999;
}
@keyframes sliderRight {
    from {
        opacity: 0;
        transform: translateX(calc(100% + 20px));
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes fadeOut {
    50% {
        opacity: 1;
    }
    75% {
        opacity: 1;
        transform: translate(5px, 5px);
    }
    100% {
        opacity: 0;
        transform: translate(calc(100% + 20px), 5px);
    }
}

.notice {
    display: flex;
    align-items: center;
    padding: 8px 0px;
    margin: 10px 0;
    background-color: #fff;
    border-radius: 8px;
    border-left: 8px solid #0271ff;
    border-top: 1px solid #d1d1d1;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    transition: all linear 0.3s;
    transition-property: transform, opacity;
    will-change: transform;
    animation: sliderRight ease 0.4s, fadeOut linear 3s forwards;
    &:hover {
        opacity: 0.8;
    }
    &_icon {
        padding: 0 12px 0 6px;
        height: 30px;
        img {
            width: 30px;
        }
    }
    &_body {
        flex-grow: 1;
    }
    &_title {
        font-size: 1.6rem;
        line-height: 1.6rem;
        padding: 8px 0;
        font-family: $font-slab;
        color: rgba(0, 0, 0, 0.7);
        letter-spacing: 0.6px;
    }
    &_msg,
    p {
        color: #757575;
        font-size: 1.4rem;
        padding: 6px 0;
        line-height: 1.6rem;
    }
    &_close {
        font-size: 20px;
        padding: 0 16px;
        opacity: 0.7;
        cursor: pointer;
        img {
            width: 24px;
        }
        &:hover {
            opacity: 0.4;
        }
    }
}
